import React, { useState, useEffect } from "react";
import "./App.css";

const propArr = ["Transfer Amount", "User", "Line of"];

function App() {
  const [amt, setAmt] = useState("");
  const [lob, setLob] = useState("");
  const [user, setUser] = useState("");

  useEffect(() => {
    // Update the document title using the browser API
    setAmt(window.location.search);
    const paramArr = window.location.pathname.split("/");
    const amount = paramArr[1];
    const lob = paramArr[2];
    const user = paramArr[4];
    setAmt(amount);
    setLob(lob);
    setUser(user);
  }, [amt, lob, user]);

  const handleUpdateText = (t: boolean) => {
    console.log(t ? "approved" : "cancelled");
  };

  return (
    <div className="App">
      <header className="App-header">
        <p>(WIP) Dispensary transaction confirmation page.</p>
        <img src={"http://buffalotraks.com/images/logo-default-95x80.png"} alt="logo" />
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            width: "60%",
            margin: "6rem",
          }}
        >
          <div style={{ display: "flex", flexDirection: "row" }}>
            <div style={{ width: "20rem", alignItems: "center" }}>Transfer Amount</div>
            <div style={{ marginLeft: "3rem", marginBottom: "2rem" }}>${amt}.00</div>
          </div>
          <div style={{ display: "flex", flexDirection: "row" }}>
            <div style={{ width: "20rem", alignItems: "center" }}>User</div>
            <div style={{ marginLeft: "3rem", marginBottom: "2rem" }}>{user}</div>
          </div>
          <div style={{ display: "flex", flexDirection: "row" }}>
            <div style={{ width: "20rem", alignItems: "center" }}>Line of Business (Location)</div>
            <div style={{ marginLeft: "3rem", marginBottom: "2rem" }}>{lob}</div>
          </div>
        </div>

        <div style={{ display: "flex", flexDirection: "row", justifyContent: "space-around", width: "40%" }}>
          <button style={{ fontSize: "1rem", margin: "1rem" }} onClick={() => handleUpdateText(false)}>
            Deny Transaction
          </button>
          <button style={{ fontSize: "1rem", margin: "1rem" }} onClick={() => handleUpdateText(true)}>
            Approve Transaction
          </button>
        </div>
      </header>
    </div>
  );
}

export default App;
